:root {
    --button-height: 40px;
    --font-size-h2: 20px;

}

body {
    background-color: #2298FF;
    font-family: Arial, Helvetica, sans-serif;
    display: flex;
    justify-content: center;
}

/* app */
.appContainer {
    width: 300px;
}

.appContainer h1 {
    color: #fff;
    font-size: 50px;
    text-align: center;
}
.appContainer h2 {
    font-size: var(--font-size-h2);
    color: black;
    text-align: center;
}

/* contact item */

.contact {
    align-items: center;
    background-color: #fff;
    border: 4px solid black;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 10px 0;
    padding: 20px;
}

.contact button {
    align-self: center;
    background-color: #D3E6FF;
    border: none;
    color: #2E3744;
    font-size: 16px;
    height: var(--button-height);
    width: 80px;
}

.contact img {
    /* margin-right: 20px; */
    width: 40px;
    height: 40px;
}

.contact__person #name {
    color: #000;
    font-size: 18px;
    font-weight: 600;
    margin: 0 0 2px;
}

.contact__person #number {
    color: #aab3bf;
    font-size: 16px;
    font-weight: 500;
    margin: 0;
}



/* search contacts */

.search input {
    border: none;
    padding: 12px;
    width: calc(100% - 24px);
}

/* add contact */

div.addContact__input {
    width: 100%;
    padding: 0;
    margin-top: 0;
}

.addContact #inputContact {
    background-color: #fff;
    /* border: 3px solid white; */
    border: none;
    margin: 0 0 8px;
    padding: 12px;
    width: calc(100% - 24px);
}

#inputContact:focus {
    border: 3px solid black;
}

.addContact button {
    background-color: #F9D745;
    /* background-image: linear-gradient(to right, #FD757B, #DC74FC); */
    border: none;
    height: var(--button-height);
    width: 100%;
}

/* notifications */

.notification {
    background-color: #DB8673;
    color: black;
    font-size: 16px;
    padding: 10px;
    margin-bottom: 10px;
}